.gift-card-main-wrap {
    display: flex;
    padding: 3rem;
    border-radius: 1rem;
    border: .1rem solid #d6d6d6;
}

.gift-card-main-wrap .gift-card-image-wrap {
    width: 50%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-flex-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.gift-card-main-wrap .gift-card-image-wrap .card-image-wrap {
    width: 80%;
    margin: 0 auto;
    height: 100%;
    overflow: hidden;
}

.gift-card-main-wrap .gift-card-image-wrap .card-image-wrap .cover-background-image-wrap {
    overflow: hidden;
    position: relative;
}

.gift-card-main-wrap .gift-card-image-wrap .card-image-wrap .cover-background-image-wrap img {
    width: 100%;
    vertical-align: middle;
}

.gift-card-main-wrap .gift-text-wrap {
    width: 50%;
}

.text-18 {
    font-size: 1.4rem;
    letter-spacing: .036rem;
}

.text-16 {
    font-size: 1.2rem;
    letter-spacing: .032rem;
    margin: 10px 0px;
}

.text-12 {
    font-size: 1rem;
    letter-spacing: .024rem;
}

.custom-checkbox {
    width: 30px;
    margin-right: 15px;
    height: 25px;
}

.gift-card-msg {
    margin: 10px 0px;
    font-size: 1.6rem;
    line-height: 2.5rem;
}

.text--inputbox {
    display: flex;
    flex-direction: column;
}

.input-control {
    border-radius: 0.8rem;
    border: 0.1rem solid #d6d6d6;
    box-shadow: none;
    background-color: #ffffff73;
    display: block;
    width: 100%;
    padding: 0.8rem 1.4rem;
    color: #000;
    outline: none;
    transition: all .2s ease;
    resize: none;
}


.step--view-content .stepview--summary-list ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.step--view-content .stepview--summary-list ul li {
    background-color: #fff;
    border-radius: 0.8rem;
    padding: 1rem;
    border: 0.1rem solid #e8e8e8;
    margin-bottom: 1rem;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}

.step--view-content .stepview--summary-list ul li .product--img {
    border: 0.1rem solid #d6d6d6;
    border-radius: 0.4rem;
    height: 4rem;
    width: 5rem;
    overflow: hidden;
    background-color: #ddd9d959;
}

.step--view-content .stepview--summary-list ul li .product--img img {
    width: 100%;
    max-width: 100%;
    /* height: auto; */
    height: 100%;
}

.step--view-content .stepview--summary-list ul li .product--title {
    padding: 0 1rem;
    max-width: 20rem;
    flex: 0 10 10rem;
    line-height: 1.4rem;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
}

.step--view-content .stepview--summary-list ul li .product--qty {
    color: #4f4f4f;
    line-height: 1.641rem;
    margin-left: auto;
    min-width: 6rem;
}

.step--view-content .stepview--summary-list ul li .product--price {
    color: black;
    font-size: 1.2rem;
    letter-spacing: .032rem;
    font-weight: 600;
}

.step--view-content .stepview--summary-list .totalAmout {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.step--view-content .stepview--summary-list .totalAmout .product--totalamt {
    margin-left: auto;
    line-height: 2.109rem;
    color: #000;
    font-weight: 600;
}

.step--view-content .stepview-btn {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}