#aboutUsContainer .team{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
margin: 50px 0;
}

#aboutUsContainer .team img{
    height:250px;
    width:250px;
    }

    #aboutUsContainer .team .box-text{
        font-size:20px;
        }

        #aboutUsContainer .heading{
            font-size:28px;
            font-weight:600;
            margin-bottom:20px;
        }   
        
        
 @media screen and (max-width:720px){
    #aboutUsContainer .team img {
        height: 100%;
        width: 100%;
    }

    #aboutUsContainer .team{
        margin: 20px 0;
        }
 }       

