.fixed-cart-items {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background: #fff;
    border-top: 1px solid #d6d6d6;
    display: flex;
    justify-content: space-between;
    height: 8rem;
}

.fixed-cart-items .fixed-cart-items-wrap {
    padding: 2rem;
    display: flex;
    overflow: auto;
    overflow-y: hidden;
}

.fixed-cart-items .fixed-cart-items-wrap .single-tem {
    margin-right: 2rem;
    max-width: 6rem;
    flex: 0 0 6rem;
    position: relative;
}

.fixed-cart-items .fixed-cart-items-wrap .single-tem .single-item-txt-price .text {
    white-space: nowrap;
    margin-bottom: 0px;
    margin-top: 0.5rem;
    font-size: 0.8rem;
    line-height: 0.968rem;
    color: rgb(0, 0, 0);
    overflow: hidden;
    text-overflow: ellipsis;
}

.fixed-cart-items .fixed-cart-items-wrap .single-tem .remove-item {
    right: -0.5rem;
    cursor: pointer;
    position: absolute;
    right: 0px;
    background: rgb(214, 214, 214);
    color: rgb(60, 60, 60);
    text-align: center;
    top: -0.5rem;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fixed-cart-items .fixed-cart-items-wrap .single-tem .count {
    font-weight: 600;
    left: -0.5rem;
    color: rgb(255, 255, 255);
    background-color: rgb(194, 35, 35);
    font-size: 0.8rem;
    line-height: 0.968rem;
    position: absolute;
    right: 0px;
    width: 1.5rem;
    height: 1.5rem;
    text-align: center;
    top: -0.5rem;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer {
    padding: 2rem 3rem 2rem 0rem;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    justify-content: flex-end;
    width: 66rem;
}

.fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .cart-total {
    margin-right: 1rem;
    display: block;
    font-weight: 600;
    font-size: 1.5rem;
    letter-spacing: .04rem;
    margin-left: auto;
    width: 100%;
}

.fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .btn--move {
    margin-left: 1rem;
}

.btn-secondary {
    font-weight: 600;
    background-color: #ddd9d9;
    border: 0.01rem solid #d6d6d6;
    padding: 1rem 1rem;
    border-radius: 0.8rem;
    letter-spacing: .036rem;
    text-transform: uppercase;
    font-size: 1rem;
    line-height: 2.1rem;
    transition: all .2s ease;
    width: 8rem;
    color: #000000;
}

.btn-primary.btn-small {
    width: 8rem;
}

.btn-primary {
    font-weight: 600;
    color: #fff;
    background-color: black;
    border: 0;
    padding: 1rem 1rem;
    border-radius: 0.8rem;
    letter-spacing: .036rem;
    text-transform: uppercase;
    font-size: 1rem;
    line-height: 2.1rem;
    cursor: pointer;
    transition: all .2s ease;
}

.img-box {
    display: flex;
    justify-content: center;
}

.img-box img {
    width: 50px;
    height: 50px;
}

.fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .cart-phone-total-price {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .cart-phone-btn {
    display: flex;
    align-items: center;
}


@media screen and (max-width:576px) {
    .fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer {
        padding: 0;
        justify-content: center;
    }

    .fixed-cart-items {
        height: 20rem;
    }

    .fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .cart-phone-total-price {
        flex-direction: column;
        align-items: center;
    }

    .fixed-cart-items.sticky-footer-wrap .cart-total-btn-footer .cart-phone-total-price .cart-total-price {
        text-align: center;
        margin-bottom: 1rem;
    }
}