    padding-right: 1rem;
    max-width: calc(100% - 3rem);
} */

.stepview-section .stepview-list {
    max-width: 66rem;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    margin: 0 auto;
    padding: 0;
    list-style: none;
}

.stepview-section .stepview-list li {
    -webkit-box-flex: 1;
    flex: 1;
    max-width: 100%;
    position: relative;
}

.stepview-section .stepview-list li.active .stepview-img {
    border-color: #c22323;
}

.stepview-section .stepview-list li .stepview-img {
    background-color: #faf8f6;
    position: relative;
    height: 6rem;
    width: 6rem;
    border-radius: 100%;
    overflow: hidden;
    border: 0.3rem solid #000;
    margin-bottom: 1.5rem;
    font-size: xxx-large;
    display: flex;
    font-weight: normal;
    justify-content: center;
    align-items: center;
}

.stepview-section .stepview-list li.active .stepview-img {
    filter: brightness(1);
}

.stepview-section .stepview-list li .stepview-img {
    filter: brightness(.4);
}

.stepview-section .stepview-list li.step-1 .stepview-subtitle {
    padding-left: 0.3rem;
}

.stepview-section .stepview-list li.active .stepview-subtitle {
    color: #c22323;
}

.stepview-section .stepview-list li .stepview-subtitle {
    text-transform: uppercase;
}

.stepview-section .stepview-list li .stepview-subtitle {
    color: #999;
}

.bold-sm {
    font-weight: 600;
}

