
.daterangepicker {
    width:43.29vw;
}

.daterangepicker .drp-calendar {
    display: none;
    width: 20.5vw;
    max-width: 20.5vw;
}

.daterangepicker .calendar-table th, .daterangepicker .calendar-table td, .daterangepicker .calendar-table td[data-title="r0c1"] {
    white-space: nowrap;
    text-align: center;
    vertical-align: middle;
    min-width: 2.92vw;
    width: 2.92vw;
    height: 4.69vh;
    line-height: 5px;
    font-size: 0.75rem;
    border-radius: 4px;
    border: 1px solid transparent;
    white-space: nowrap;
    cursor: pointer;
}

.daterangepicker .drp-calendar.left {
    margin-right:0.625rem;
}

.daterangepicker td.in-range {
    background-color: #E3E7EB;
}

.daterangepicker td.active, .daterangepicker td.active:hover {
    background-color: #072646;
    border-color: transparent;
    color: #fff;
}

.daterangepicker .drp-buttons .applyBtn.btn{
    /* background:#3A5978; */
    border:none;
    line-height: 1rem;
    border-radius:5px;
    padding-top:5px;
    padding-bottom:5px;
}

.daterangepicker .drp-buttons .applyBtn.btn:hover{
    background:rgba(0,0,0,0.7)
}

form.dateRange{
    background: transparent url("background-image: url(data:image/svg+xml;base64,") no-repeat 13px center;
}

.daterangepicker .drp-buttons .cancelBtn.btn{
    border:1px solid #E3E7EB;
    border-radius:4px;
    color:#4A4E52;
}

.daterangepicker .drp-selected {
    font-size: 0.8125rem;
    padding-right: 0.5rem;
    padding: 0.375rem 0.625rem;
    float: left;
    border: 1px solid #A6B0B9;
    border-radius: 4px;
    background:#F8F9FA;
}