.gift-set {
    display: flex;
    flex-direction: column;
}

.gift-set .heading {
    font-weight: 600;
    line-height: 3.281rem;
    color: #000;
    text-transform: uppercase;
    font-size: 2.8rem;
    align-self: center;
    letter-spacing: .028rem;
}

.gift-set .sub-heading {
    font-weight: 500;
    line-height: 3.281rem;
    color: #000;
    text-transform: uppercase;
    font-size: 1.8rem;
    align-self: center;
}

@media screen and (max-width:576px) {
    .gift-set .heading {
        font-size: 1.8rem;
    }
}