.giftProductCard .addtoCart {
    background: #000;
    color: #fff;
    border-radius: 5px;
    width: 60%;
}

.giftProductCard .cardTitle {
    font-weight: 500;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.giftProductCard .discountedPrice {
    font-weight: 600;
    font-size: 18px;
    white-space: nowrap;
}

.giftProductCard .originalPrice {
    color: rgba(0, 0, 0, 0.4);
    text-decoration: line-through;
    margin-right: 5px;
    font-size: 12px;
}

.giftProductCard .productImage {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.giftProductCard .seperator {
    width: 1px;
    background: rgb(0, 0, 0);
    height: 100%;
    margin: 5px;
}

.giftProductCard .discount {
    font-size: 10px;
    color: green;
    font-weight: 500;
}

.giftProductCard .attributeValue {
    font-size: 14px;
}

.giftProductCard .cardImage {
    height: 100%;
    width: 100%;
}

.giftProductCard .ratingCount {
    font-weight: 500;
    font-size: 14px;
}

.giftProductCard {
    text-decoration: none;
    color: #000;
    text-align: center;
    margin-bottom: 1rem;
}

@media screen and (max-width:520px) {
    .productsDetails {
        min-height: 290px;
        position: relative;
    }

    .productsDetails .addtoCart {
        position: absolute;
        left: 5%;
        bottom: 5%;
        width: 90%;
    }
}