#TncContainer{
    background:#fff;
}

.bannerTnc{
    background:#000;
    color:#fff;
    min-height:180px;
    font-size:32px;
    font-weight:600;
    display: flex;
    align-items: center;
    justify-content: center;
}

#TncContainer .heading{
font-weight:600;
font-size:20px;
padding:10px;
}

#TncContainer .title{
    margin:10px;
    text-align: center;
    font-size:18px;
}

#TncContainer ul li{
    list-style-type: decimal;
    margin:1rem 0;
}

@media screen and (max-width:720px){
    .bannerTnc{
        min-height:100px;
        font-size:26px;
    }
}