.bannerReturns{
    background:#000;
    color:#fff;
    min-height:180px;
    font-size:32px;
    font-weight:600;
    display: flex;
    align-items: center;
    justify-content: center;
}

#returnsContainer{
    background: #fff;
}

#returnsContainer .title{
    text-align: center;
    font-size:22px;
    padding:20px 0px;
    }

#returnsContainer .subTitle{
text-align: center;
margin-bottom:40px;
}

#returnsContainer .refundCancellation span{
margin:20px 0px;
}

#returnsContainer .headingTitle{
    text-align: center;
    font-size:32px;
    font-weight:600;
    margin:30px 0;
}

#returnsContainer .heading{
    font-size:20px;
    font-weight:600;
}

@media screen and (max-width:720px){
    .bannerReturns{
            min-height:100px;
        font-size:26px;
    }
}