.wishlist-cart-container {
    padding: 1rem 1rem;
}

.wishlist-cart-container .product-details-section {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.wishlist-cart-container .product-details-section .product-thumbnail {
    width: 10rem;
}

.wishlist-cart-container .product-details-section .product-thumbnail img {
    width: 100%;
    height: 100%;
}

.wishlist-cart-container .product-price-section {
    display: flex;
    align-items: center;
    justify-content: center;
}

.wishlist-cart-container .product-stock-section {
    display: flex;
    align-items: center;
    justify-content: center;
}

.wishlist-cart-container .product-name {
    width: 10rem;
}